.text {
    color: #C3C3C2;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.chart-container {
    padding: 40px 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.reindex-button {
    width: 200px;
    margin-top: 50px;
    margin-left: 50px;
}

.chart-container > div, .reindex-button {
    margin-top: 20px;
}

.chart-container > div, MyBarChart {
    margin-top: 50px;
}